.main-div {
    background-color: rgba(246, 246, 234, 0.425);
padding-top: 54px;
}

.contact-heading {
    margin-top: 10px;
    text-align: center;
    font-size: 40px;
    letter-spacing: 2;
    margin-bottom: 35px;
}

.contact-heading::before {
    content: "";
    display: block;
    width: 50px;
    height: 5px;
    border-radius: 2px;
    background: rgb(109, 131, 242);
    position: relative;
    top: 70px;
    left: 48%;

}

.container-contact{
display: grid;
grid-template-columns: auto auto;
justify-content: center;
align-items: center;
gap: 90px;

width: auto;
margin: 0px 90px;
}
.contact-p {
    text-align: center;
    font-size: 20px;
    margin-bottom: 40px;

    letter-spacing: 1px;
}
.container-1 h1{
    font-size: 45px;
}

.container-1 p{
    color: rgba(75, 74, 74, 0.694);
    font-size: 20px;
}


.container-2 {
    text-align: center;
    margin: auto;
    
}

.contact-form {
    width: 500px;
    margin: auto;
    border: 1px solid rgba(0, 0, 0, 0.215);
border-radius: 10px;
padding: 20px;
background-color: rgba(220, 220, 220, 0.256);
}

.contact-inputs {
    display: flex;
    flex-direction: column;
    gap: 30px;


}

.contact-inputs h2{
    text-align: left;
}
.contact-inputs input {
    padding: 10px;
   border-radius: 10px;
   border: none;
   box-shadow: .5px .5px 1px  rgba(0, 0, 0, 0.224);
}
textarea{
    border-radius: 10px;
    padding: 10px;
    border: none;
    resize: none;
    box-shadow: .5px .5px 1px  rgba(0, 0, 0, 0.224);
}

.submit {
    cursor: pointer;
    transition: all 0.2s;
    width: fit-content;
    padding: 5px 20px;
    border-radius: 5px;
    font-size: 20px;
    
    background-color: blueviolet;
    color: white;
    font-weight: 500;
}

.submit:hover {
    background-color: rgb(255, 255, 255);
    border: 1px solid black;
    color: rgb(8, 8, 8);
    transform: scale(0.9);
}

@media screen and (min-width:370px) and (max-width:480px){
    .container-contact{
        display: grid;
        grid-template-columns: auto;
        justify-content: center;
        align-items: center;
        gap: 40px;
        
        width: auto;
        margin: 0px 90px;
        }

        .contact-heading {
            margin-top: 10px;
            text-align: center;
            font-size: 40px;
            letter-spacing: 1.5;
            margin-bottom: 35px;
        }
        
        .contact-p {
            text-align: center;
            font-size: 17px;
            margin-bottom: 40px;
        
            letter-spacing: 1px;
        }

        .contact-form {
            width: 100%;
            max-width: 600px;
            min-width: 370px;
            margin: auto;
            border: 1px solid rgba(0, 0, 0, 0.215);
        border-radius: 10px;
        padding: 20px;
        background-color: rgba(220, 220, 220, 0.256);
        }

        .container-1 h1{
            font-size: 40px;
        }
        
}

@media screen and (min-width:481px) and (max-width:1024px){
    .container-contact{
        display: grid;
        grid-template-columns: auto auto;
        justify-content: center;
        align-items: center;
        gap: 60px;
        
        width: auto;
        margin: 0px 50px;
        }
        .contact-form {
            width: 100%;
            max-width: 600px;
            min-width: 370px;
            margin: auto;
            border: 1px solid rgba(0, 0, 0, 0.215);
        border-radius: 10px;
        padding: 20px;
        background-color: rgba(220, 220, 220, 0.256);
        }


        .contact-p {
            text-align: center;
            font-size: 15px;
            margin-bottom: 60px;
            letter-spacing: 2;
        }
        .container-1 h1{
            font-size: 30px;
        }
        
        .container-1 p{
            color: rgba(75, 74, 74, 0.694);
            font-size: 15px;
        }
}