
.div-back{
    position: absolute;
    left: 0px;
    width: 100%;
    min-height: 500px;
    background: linear-gradient(134deg, rgb(106, 152, 240) 0%, rgb(73, 97, 220) 99%);
    transform: skewY(-3deg);
    z-index: -1;
    pointer-events: none;
 margin-top: 20px;

}

.main-divabout{
    padding-top: 20px;
    margin-top: 100px;
 box-sizing: border-box;
}
.heading-about{
margin: 20px;
    font-size: 40px;
    color: rgb(0, 0, 0);
    text-align: center;
    letter-spacing: 1;
    margin-top: 80px;

}
.heading-about::before{
    content: "";
    display: block;
    width: 50px;
    height: 5px;
    border-radius: 2px;
    background: rgb(109, 131, 242);
    position: relative;
    top: 70px;
left: 48%;
}
.section-p{
  font-weight: 600;
    text-align: center;
    font-size: 25px;
    padding-bottom: 50px;
    /* position: relative;
top: 150px; */
}
.section-p1{
    padding-top: 20px;
    font-size: 20px;
text-align: justify;
    line-height: 1.5;
    font-family: Karla, sans-serif;
    font-weight:500;

}
.get-about{
    text-align: left;
    letter-spacing: 2px;
}
.article-about {
    padding-top: 100px;
    margin: 0 100px 0 100px;



}
.button{

position: relative;
top: 20px;
text-decoration: none;
color: white;
padding: 10px;
border-radius: 10px;
font-size: 18px;
z-index: 1;
box-shadow:  1px 1px 5px rgba(0, 0, 0, 0.486);
}
.button::before{
    content: '';
    width: 0;
    height: 40px;
    border-radius: 10px;
    background-image: linear-gradient(to right, rgba(0, 0, 255, 0.541) 0%,rgba(137, 43, 226, 0.55) 100%);
    transition: .5s ease;
    display: block;
    z-index: -1;
    position: absolute;
    top: 0;
    left: 0;
}
.button:hover::before{
width: 9.6em;
}
