@media screen and (min-width:370px)and (max-width: 480px){
    .section-p{
        font-size: 15px;
        text-align: center;
        padding-top: 50px;
margin: 0px 10px;
padding: 30px 0px;
       
    }
.article-about{
    padding: 0;
    margin: 0;
}
    .get-about{
        margin: 0px 10px;
        font-size: 25px;
    }
    .section-p1{
        margin: 0px 20px;
        font-size: 16px;
    }
    .button{
      top: 10px;
      margin: 0px 20px;
    }
    .main-divabout{
        margin-bottom: 100px;
         /* width: 100%;  */
    }
    
}

@media screen and (min-width:481px)and (max-width: 1024px){
    .section-p{
        font-size: 12px;
        text-align: center;
        padding-top: 50px;
padding-bottom: 0;
margin: 0;
       
    }
.article-about{
    padding: 0;
    margin: 0;
}
    .get-about{
        margin: 0px 10px;
        font-size: 25px;
    }
    .section-p1{
        margin: 0px 35px;
        font-size: 10px;
        line-height: 1.8;
    }
    .button{
      top: 10px;
      margin: 0px 20px;
      font-size: 10px;
    }
    .main-divabout{
        margin-bottom: 100px;
        /* width: 100%; */
    }
    .div-back{
        min-height:600px ;
    }
}