
.projects-container{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}
.heading-projects {
    margin-top: 80px;
    text-align: center;
    font-size: 40px;
    letter-spacing: 2;
    margin-bottom: 35px;
}

.heading-projects::before {
    content: "";
    display: block;
    width: 50px;
    height: 5px;
    border-radius: 2px;
    background: rgb(109, 131, 242);
    position: relative;
    top: 70px;
    left: 48%;

}

.img-1 {
    width: 100%;
    object-fit: cover;
    display: block;

}

.img-div {
    width: 700px;
    margin-left: 50px;
}

.prj {

    display: grid;
    grid-template-columns: .5fr 1fr;
    grid-gap: 50px;
  margin: 0px 50px;
  margin-bottom: 100px;
}

.prj-desc {

    display: flex;
    justify-content: center;
    flex-direction: column;
    align-items: flex-start;
}

.heading-h2 {
    font-weight: 700;
    font-size: 30px;
    margin-bottom: 20px;
}

.description {
    font-size: 20px;
    color: #666;
    max-width: 35rem;
    line-height: 1.7;
    margin-bottom: 20px;
    text-align: justify;
}

.case-study-bt {

    padding: 10px 20px 10px 20px;
    border: 1px solid blueviolet;
    text-decoration: none;
    color: whitesmoke;
    background-color: blueviolet;
    border-radius: 8px;
    font-weight: 700;
}

.desc-p {
    text-align: center;
    font-size: 20px;
    margin-bottom: 80px;

    letter-spacing: 1px;
}
@media screen and (min-width:370px) and (max-width:480px)  {
  

    .desc-p {
        text-align: center;
        font-size: 15px;
        margin-bottom: 60px;
    
        letter-spacing: 1px;
    }
.projects-container{
    margin: 0 30px;
}
.img-div {
    width: 350px;
