.main-skills{
    margin-top: 200px;
padding-top: 95px;
}
.skill-heading{
    
position: relative;
bottom: 20px;

    font-size: 40px;
    font-weight: 900;
    /* color: rgb(109, 131, 242); */
    color: black;
    text-align: center;
    letter-spacing: 1px;
}
.skill-heading::before{
    content: "";
    display: block;
    width: 50px;
    height: 5px;
    border-radius: 2px;
    background: rgb(109, 131, 242);
    position: relative;
    top: 70px;
left: 50%;
}
.div-back1{
    position: absolute;
    left: 0px;
   
    width: 100%;
    min-height: 550px;
    /* background: linear-gradient(134deg, rgb(106, 152, 240) 0%, rgb(73, 97, 220) 99%); */
    background-color: rgba(224, 214, 236, 0.39);
    transform: skewY(3deg);
    z-index: -1;
    pointer-events: none;
    box-shadow: 1px ;
 
}
.skills-w{
    /* display: flex;
    flex-wrap: wrap;
    justify-content: space-around;
 

position: absolute; */

display: grid;
grid-template-columns:auto auto auto;

justify-content: center;

}
.items{
   width: 200px;
   height: 120px;

    padding: 15px;
 margin: 30px 50px 30px 50px;
    border-radius: 5px;
    text-align: center;

}
.items img{
    width: 95px;
    height: 95px;
}

#html{
    border-bottom: 3px solid orangered;
    border-left: 1px solid orangered;
    border-right:1px solid orangered ;
    background-color: rgba(255, 68, 0, 0.107);

}

#css{
    border-bottom: 3px solid rgb(0, 145, 255);
    border-left: 1px solid rgb(0, 145, 255);
    border-right:1px solid rgb(0, 145, 255) ;
    background-color: rgba(0, 145, 255, 0.107);
}
#javascript{
    border-bottom: 3px solid rgb(255, 225, 0);
    border-left: 1px solid rgb(255, 225, 0);
    border-right:1px solid rgb(255, 225, 0) ;
    background-color: rgb(255, 225, 0, 0.107);
}
#react{
    border-bottom: 3px solid rgb(0, 166, 255);
    border-left: 1px solid rgb(0, 166, 255);
    border-right:1px solid rgb(0, 166, 255) ;
    background-color: rgb(0, 166, 255, 0.107);
}
#node{
    border-bottom: 3px solid rgb(7, 144, 82);
    border-left: 1px solid rgb(7, 144, 82);
    border-right:1px solid rgb(7, 144, 82) ;
    background-color: rgba(7, 144, 82, 0.107);
}
#python{
    border-bottom: 3px solid rgb(0, 94, 255);
    border-left: 1px solid rgb(0, 94, 255);
    border-right:1px solid rgb(0, 94, 255);
    background:linear-gradient( rgb(0, 94, 255,0.160),rgb(242, 255, 0,0.160));
}
#java{
    border-bottom: 3px solid rgb(0, 157, 255);
    border-left: 1px solid rgb(0, 157, 255);
    border-right:1px solid rgb(0, 157, 255) ;
    background-color: rgba(0, 157, 255, 0.13);
}
#sql{
    border-bottom: 3px solid rgb(255, 187, 0);
    border-left: 1px solid rgb(255, 187, 0);
    border-right:1px solid rgb(255, 187, 0) ;
    background-color: rgba(255, 187, 0, 0.142);
}
#responsive{
    border-bottom: 3px solid rgb(183, 0, 255);
    border-left: 1px solid rgb(183, 0, 255);
    border-right:1px solid rgb(183, 0, 255) ;
    background-color: rgba(183, 0, 255, 0.091);
}

@media screen and (min-width:370px)and (max-width:600px) {
.skills-w{

    margin-top: 50px;
    grid-template-columns:auto auto;
}

.items{
    width: 150px;
    height: 100px;
    margin: 10px 30px;
     border-radius: 5px;
     text-align: center;

 }
 .items img{
    width: 70px;
    height: 70px;
}
#responsive{
    display: none;
}
}