}


  .hidden {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
  }
  

.menu a:hover {
    color: rgb(120, 78, 203);



}

.image a:hover {
    color: rgb(120, 78, 203);
}

.image {
    display: flex;
    align-items: center;
    padding-left: 100px;
}

.image a {
    text-decoration: none;
    font-weight: bold;
    font-size: 18px;
    color: black;

}

.image span {
    background-color: rgb(120, 78, 203);
    width: 55px;
    height: 58px;
    border-radius: 90px;
    position: absolute;
    left: 111px;
    top: 11px;
    z-index: 1;
}





nav {
    display: flex;
    align-items: center;
    justify-content: space-between;
   
    padding: 20px 80px;
   
}

#navbar {
    display: flex;
    align-items: center;
    justify-content: center;

}

#navbar li {
    list-style: none;
    padding: 0 20px;
    position: relative;
}

#navbar li a {
    text-decoration: none;
    font-family: system-ui, -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
    font-size: 1rem;
    font-weight: 700;
    color: black;
    transition: 0.3s ease-in-out;
}

#navbar li a:hover,
#navbar li a.active {
    color: blue;
}

#navbar li a:hover::after,
#navbar li a.active::after {
    content: "";
    width: 30%;
    height: 2px;
    background: blue;
    position: absolute;
    bottom: -4px;
    left: 20px;
}

#mobile {
    display: none;
  
}

#mobile i {
    color: rgb(17, 32, 240);
    align-items: center;
}

@media screen and (max-width:600px) {

.image{
    padding-left: 30px;
}

.image span {

    position: absolute;
    left: 41.5px;
    top: 11px;
   
}




    #navbar {
        display: flex;
        flex-direction: column;
        align-items: flex-start;
        justify-content: flex-start;
        position:fixed;
        top: 0px;
        right: -300px;
        width: 300px;
        height: 100vh;
        background-color: rgb(255, 255, 255);
        padding: 40px 0 0 10px;
        transition: 0.1s ease-in-out;
        z-index: 3;
    }

 
    #navbar li{
        margin-bottom: 25px;
     
    }
    #mobile {
        display: block;
        position: absolute;
        right: 50px;
        z-index: 4;
    }
    #mobile i {
        font-size: 24px;
        cursor: pointer;
        z-index: 5;
    }
    #navbar.active{
        right: 0px;

    }

}

