.banner{
    background: linear-gradient(to right,rgba(245, 245, 245, 0.671),rgba(245, 245, 245, 0.671)),url(../extra/common-bg.svg);
    background-position: center;
    background-size:100% auto;
    height: 100%;
    display: block;
    min-height: 45rem;
    max-height: 120rem;
    position: relative;
    box-shadow: 0 2px 10px 0 rgba(0, 0, 0, .2);
width: 100%;
padding-bottom: 10px;

}

.banner-1{
    display: flex;
    flex-direction: column;
 
    align-items: center;
}
.banner-1 img {
    margin-top: 90px;
    width: 200px;
    height: 200px;
/* position: relative;
left: 40%;
top: 100px; */

    }


    .banner-1 p{
       margin-top: 50px;
        text-align: center;
        font-size: 25px;
        letter-spacing: 3;
        font-weight:400;

    }
    .banner-h1{
margin-top: 50px;
        text-align: center;
        font-size: 55px;
        letter-spacing: 3px;
        font-weight: 800;

    }
    
.btn{
font-weight: 700;
    position: relative;
    top: 20px;
    text-decoration: none;
    color: rgb(0, 0, 0);
    padding: 15px;
    border-radius: 10px;
    font-size: 20px;
    z-index: 1;
    text-align: center;
    width: 150px;
    letter-spacing: 1.5px;
    box-shadow:  1px 1px 5px rgba(0, 0, 0, 0.486);
    }
    .btn::before{
        content: '';
        width: 0;
        height: 60px;
        border-radius: 10px;
        background-image: linear-gradient(to right, rgba(0, 0, 255, 0.541) 0%,rgba(137, 43, 226, 0.55) 100%);
        transition: .9s ease;
        display: block;
        z-index: -1;
        position: absolute;
        top: 0;
        left: 0;
        color: white;
    }
    .btn:hover::before{
    width: 7.5em;
    color: white;
 
}

.btn:hover{
    color: white;
}
    .title{
        animation-name: moveRight;
        animation-duration: 1s;
    }

    
@keyframes moveRight {
    0%{
        opacity: 0;
        transform: translateX(100px);
    }
    100%{
        opacity: 1;
        transform: translate(0);
    }
}
@keyframes moveLeft {
    0%{
        opacity: 0;
        transform: translateX(-150px);
    }
    100%{
        opacity: 1;
        transform: translate(0);
    }
}
@keyframes moveUp {
    0%{
        opacity: 0;
        transform: translateY(50px);
    }
    100%{
        opacity: 1;
        transform: translate(0);
    }
}
.text{
    font-size: 22px;
    color: rgb(39, 40, 40);

}

