.footer-main{
    margin-top: 80px ;
    background-color: rgb(0, 0, 0);
    color: white;
    padding:0px 250px;
    font-family: sans-serif;

}
a{
    text-decoration: none;
    margin: 0px 5px;
}
.footer-content{
margin-top: 20px;
font-family:Arial, Helvetica, sans-serif;
color: rgba(255, 255, 255, 0.788);

}

.footer-container{
    width: 50%;
    order: 0;
    max-width: 50rem;
}
#twitter{
    fill: rgb(255, 255, 255);
    width: 30px;
    height: 30px;
    cursor:default;
}
#twitter:hover{
    fill: rgb(0, 119, 255);
}

#linkedin{
    fill: rgb(255, 255, 255);
    width: 30px;
    height: 30px;
    cursor:default;
}

#github:hover{
    fill: rgb(116, 116, 194);
}

#github{
    fill: rgb(255, 255, 255);
    width: 30px;
    height: 30px;
    cursor:default;
} 
.footer-inner{
    display: flex;
    
    padding: 50px 0;
    justify-content: space-between;
    
}
.socials{
    margin-top: 20px;


}

.copy{
   border-top: 1px solid rgba(245, 245, 245, 0.279);
   border-radius: 2px;
   text-align: center;
   padding: 20px 0;
   font-size: 10px;
}

@media screen and (min-width:370px) and (max-width:480px) {
    .footer-inner{
        display: flex;
        flex-direction: column;
        padding: 50px 0;
        justify-content: space-between;
        
    }
    .footer-container{
        width: 100%;
        order: 0;
     
    }
    .footer-main{
        padding: 0 25px;
    }
    .socials-main{
        margin-top: 20px;
    
    
    }
    .footer-content{
        text-align: justify;
    }
a{
margin: 0 10px;
}
}